import React, { useEffect, useState } from "react";
import { getValidTagInfoDictionary } from "../../../../../../utils";
import { TagIconSolid as TagIcon } from "../../../../icon";
import { PostTagsContainer } from "./container";
import { PostTagsWrapper } from "./wrapper";

export const PostTags = ({ tags }) => {
  const [loading, setLoading] = useState(true);
  const [tagInfoDict, setTagInfoDict] = useState({});

  useEffect(() => {
    setTagInfoDict(getValidTagInfoDictionary());
    setLoading(false);
  }, []);

  if (loading) return <></>;

  return (
    <PostTagsWrapper>
      <TagIcon size={20} />
      <PostTagsContainer>
        {tags
          ?.map((tagKey, idx, fullTags) => (
            <span key={idx}>
              {tagInfoDict[tagKey].ko}
              {idx < fullTags.length - 1 ? `,` : ""}
            </span>
          ))
          .slice(0, 3)}
        {tags?.length > 3 && <span>...</span>}
      </PostTagsContainer>
    </PostTagsWrapper>
  );
};
